import Endpoint from '@/services/Endpoint'

export default {
  cardStatuses (payload) {
    return Endpoint().get('/s1/card/cardStatus', {
      params: payload
    })
  },
  createCardStatus (payload) {
    return Endpoint().post('/s1/card/cardStatus', payload)
  },
  modifyCardStatus (payload) {
    return Endpoint().patch('/s1/card/cardStatus', payload)
  }
}
